.form {
  color: $color-default-white;
}

.form h2 {
  font-size: 32px;
  font-weight: 400;
  line-height: 36px;
  margin: 0 0 24px 0;

  @include vp-1199 {
    font-size: 28px;
    line-height: 28px;
    margin-bottom: 28px;
  }

  @include vp-767 {
    font-size: 18px;
    line-height: 22px;
    margin-bottom: 17px;
  }
}

.form p {
  color: rgba($color-alabaster, 0.9);
  margin: 0 0 19px 0;

  @include vp-1199 {
    line-height: 20px;
    margin-bottom: 32px;
  }

  @include vp-767 {
    font-size: 14px;
    margin-bottom: 26px;
  }
}

.form__fields-wrapper {
  display: grid;
  grid-template-columns: 1fr;
  gap: 16px;
  margin-bottom: 43px;

  @include vp-767 {
    gap: 21px;
    margin-bottom: 29px;
  }
}

.form__field-wrapper {
  display: flex;
  flex-direction: column;
  width: 100%;
  color: rgba($color-alabaster, 0.8);
  font-size: 14px;
  line-height: 18px;
}

.form__field-wrapper--input,
.form__field-wrapper--textarea {

  label {
    display: block;
    width: fit-content;
    padding-bottom: 8px;
    transition: all $trans-default;
    /* display: inline-flex;
    align-items: center;
    margin-bottom: 12px;
    //width: 100%;

    @media screen and (max-width: 767px) {
      align-items: flex-start;
      flex-direction: column;
    }*/

    &:hover {
      //border: 1px solid red;
      color: $color-alabaster;

      input,
      textarea {
        border: 1px solid $color-default-white;
        //background-color: $color-transparent;
      }
    }

    @include vp-767 {
      font-size: 12px;
      line-height: 14px;
      padding-bottom: 11px;
    }
  }

  input,
  textarea {
    // flex-grow: 1;
    // max-width: 300px;
    order: 1;
    // margin-left: auto;
    padding: 14px 25px;

    border: 1px solid rgba($color-default-white, 0.3);
    border-radius: 8px;
    outline: none;
    background-color: $color-transparent;

    transition: all $trans-default;
    font-size: 14px;
    font-weight: 400;
    line-height: 14px;
    color: $color-default-white;

    /* @media screen and (max-width: 767px) {
      max-width: unset;
      width: 100%;
    }*/

    &:hover {
      border: 1px solid $color-default-white;
      //background-color: $color-transparent;

      & + label {
        color: $color-alabaster;
      }
    }

    &:focus,
    &:active {
      border: 1px solid $color-default-white;
      //background-color: $color-transparent;

      & + label {
        color: $color-alabaster;
      }
    }

    &:disabled {
      border: 1px solid rgba($color-default-white, 0.4);
      background-color: rgba($color-default-white, 0.2);

      & + label {
        color: rgba($color-alabaster, 0.6);
      }
    }
  }

  input {
    min-height: 50px;
  }

  textarea {
    min-height: 98px;

    @include vp-767 {
      min-height: 94px;
    }
  }

  /* &__label {
    @media screen and (max-width: 767px) {
      margin: 0 0 18px 0;
    }
  } */

  /* &__error {
    position: absolute;
    right: 0;
    bottom: 35px;

    color: #ff1553;

    opacity: 0;

    transition: opacity 0.3s ease;

    @media screen and (max-width: 767px) {
      bottom: 20px;
    }
  } */

  &.is-invalid {
    input,
    textarea {
      border: 1px solid $color-wild-watermelon;
      background-color: rgba($color-wild-watermelon, 0.2);

      &:focus,
      &:hover {
        border: 1px solid $color-default-white;
        background-color: $color-transparent;
      }
    }
  }

  /* &.is-valid {
    input {
      border: 2px solid #22b259;

      &:focus,
      &:hover {
        border: 2px solid rgba(#22b259, 0.5);
      }
    }
  } */
}

.form__field-wrapper--select {
  margin-bottom: 4px;

  label {
    display: block;
    width: fit-content;
    padding-bottom: 12px;
    transition: all $trans-default;
    order: -1;

    &:hover {
      color: $color-alabaster;

      &__button {
        border: 1px solid $color-default-white;
        //background-color: $color-transparent;
      }
    }

    @include vp-1199 {
      padding-bottom: 8px;
    }

    @include vp-767 {
      font-size: 12px;
      line-height: 14px;
      padding-bottom: 11px;
    }
  }

  @include vp-767 {
    margin: 0;
  }
}

.custom-toggle {
  position: relative;
  font-size: 12px;
  line-height: 12px;

  display: flex;
  //width: 500px;
  //padding: 14px 0 10px 36px;

  /*@media screen and (max-width: 767px) {
    padding: 14px 0 10px 0;
    width: 100%;
  }*/

  /*&:last-of-type {
    padding: 14px 0 58px 36px;

    @media screen and (max-width: 767px) {
      padding: 14px 0 58px 0;
    }
  }*/

  input {
    position: absolute;

    width: 1px;
    height: 1px;
    margin: -1px;

    clip: rect(0 0 0 0);

    &:hover + .custom-toggle__icon,
    &:focus + .custom-toggle__icon {
      border: 1px solid $color-default-white;
      background-color: $color-transparent;
    }

    &:disabled + .custom-toggle__icon {
      border: 1px solid $color-default-white;
      background-color: rgba($color-default-white, 0.6);
    }
  }

  label {
    position: relative;

    padding: 5px 0 0 30px;

    cursor: pointer;

    span {
      margin-bottom: 0;
      margin-left: 0;

      @include vp-767 {
        line-height: 14px;
      }
    }

    @include vp-767 {
      padding: 0 0 0 30px;
    }
  }

  &--checkbox {
    .custom-toggle__icon {
      position: absolute;
      top: 0;
      left: 0;

      width: 20px;
      height: 20px;
      border-radius: 2px;

      border: 1px solid rgba($color-default-white, 0.7);
      background-color: $color-transparent;

      transition: all $trans-default;
      // color: $color-default-white;

      &::before {
        content: "";
        position: absolute;
        top: 50%;
        left: 50%;

        width: 10px;
        height: 10px;

        background-color: $color-transparent;


        background-image: url("../img/svg/icon-checkbox.svg");
        background-repeat: no-repeat;
        background-size: 100%;

        transform: translate(-50%, -50%);

        transition: all $trans-default;
        // color: $color-cerulean-blue;
        opacity: 0;
      }
    }
  }

  /*&--radio {
    .custom-toggle__icon {
      position: absolute;
      top: 0;
      left: 0;

      width: 28px;
      height: 28px;

      border: 1px solid #2c39f2;
      border-radius: 50%;

      transition: border 0.3s ease;

      &::before {
        content: "";
        position: absolute;
        top: 50%;
        left: 50%;

        width: 20px;
        height: 20px;

        color: rgba(255, 255, 255, 0);

        border-radius: 50%;
        transform: translate(-50%, -50%);

        transition: color 0.3s ease;
      }
    }
  }*/

  input:checked {
    & + .custom-toggle__icon {
      background-color: $color-default-white;

      &::before {
        opacity: 1;
      }
    }

    &:hover + .custom-toggle__icon,
    &:focus + .custom-toggle__icon {
      background-color: $color-transparent;
      border: 1px solid $color-default-white;

      &::before {
        background-image: url("../img/svg/icon-checkbox-white.svg");
      }
    }

    &:disabled + .custom-toggle__icon {
      background-color: $color-mischka;
      border: 1px solid $color-mischka;

      &::before {
        background-image: url("../img/svg/icon-checkbox-white.svg");
      }
    }
  }



  &.is-invalid {
    .custom-toggle__icon {
      border: 1px solid rgba($color-wild-watermelon, 0.7);
      background-color: rgba($color-wild-watermelon, 0.2);
    }
  }

  /*.input-message {
    position: absolute;
    right: 0;
    bottom: -5px;

    &.is-invalid {
      color: #ff0000;
    }

    &.is-valid {
      color: #15ab07;
    }
  }*/
}

.form__button {
  display: flex;
  width: 100%;
  text-align: center;

  span {
    flex-grow: 1;
  }
}

